import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';


import { Vendor } from '../model/vendor';
import { VendorService } from '../vendor/vendor.service';
import { SaveVendorService } from '../save-vendor.service';

@Component({
  selector: 'app-save-vendor',
  templateUrl: './save-vendor.component.html',
  styleUrls: ['./save-vendor.component.scss']
})
export class SaveVendorComponent implements OnInit {
  vendor=new Vendor();
  vendors:Vendor[];
  vendorId:any;

  constructor(private service :SaveVendorService,private router:Router,private route:ActivatedRoute,private viewService:VendorService) { }

  ngOnInit() {
     this.route.params.subscribe(params => this.getVendorById(params['id']));
    this.resetForm();
  }


  addVendor(): void{
    this.service.insertVendor(this.vendor)
    .subscribe((response)=>{

      console.log(response);
      this.getVendor();
      this.router.navigate(['vendor'])
      this.resetForm();

    },(error)=>{
      console.log(error);
    
    });
  }
  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={ 
    vendorId:null,
    vendorName:'',
    addr:'',
    location:'',
    service:'',
    pincode:'',
    isActive:'',
    contactId:'',
    name:'',
    dep:'',
    email:'',
    phone:''                      
    }                                  
  }

  
 
  view(){
    this.router.navigate(['vendor']);
  }
  getVendor():void{
    
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);
    },(error)=>{
      console.log(error);
    });
  }

      
     
  getVendorById(vendorId: string){
    this.vendorId =vendorId;
    this.service.getVendorbyId(this.vendorId)
    .subscribe((vendorbyId)=>{
      this.vendor=vendorbyId;
      console.log(vendorbyId);
    },(error)=>{
      console.log(error);
    });
  }
//to navigate to back page
  back(){
  this.router.navigate(['vendor']);
} 
//navigate to login
logout(){
  this.router.navigate(['login']);
}

}

