import { Component, OnInit } from '@angular/core';
import { Vendor } from '../model/vendor';
import { Router } from '@angular/router';
import { VendorService } from './vendor.service';

@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.scss']
})
export class VendorComponent implements OnInit {
  formData: { vendorId: any; vendorName: string; addr: string; location: string; service: string; sAddr: string; pincode: any; name: string; dep: string; email: string; phone: string; };
  
  vendors:Vendor[];
  vendor=new Vendor();
  searchid:any;
  
  constructor(private router: Router,private service:VendorService) { }

  ngOnInit() {
    this.getAllVendors(); 
  }
  getAllVendors(){
    this.service.getVendor().subscribe((vendorData)=>{
      this.vendors=vendorData
      console.log(vendorData)
    }, (error)=>{
      console.log(error);
  });
}
  log(){
    this.router.navigate([''])
  }
  view(){
    this.router.navigate(['vendor'])
  }
  addVendor(){
    this.router.navigate(['save-vendor'])
  }
  disable(vendorId:any):void{
    console.log("inside disable"+this.searchid);
    this.service.disablevendor(this.searchid)
      .subscribe((vendorData)=>{
        console.log(vendorData);
        this.getAllVendors();
      }, (error) =>{
        console.log(error);
       
      });
      
     
  }
  getContactDetails(data:any){
    console.log(data);
    this.view=data.vendorId;
    this.viewContact;
    this.viewContact(this.view);
  
}
viewContact(data){
  this.router.navigate(['contact',this.view])
}

// navigate to editing page

editVendor(data:any){
  this.router.navigate(['save-vendor',data])
}

//to get all vendors
getVendors() {
  this.service.getVendor()
    .subscribe((data) => {
      this.vendors = data;
      console.log(data);
    }, (error) => {
      console.log(error);
    });
}
onSelect(selectedVendor: any) {
  console.log("Selected item Id:", selectedVendor.vendorId);
  this.searchid = selectedVendor.vendorId;
}

}

