import { Component, OnInit } from '@angular/core';
import { Login } from '../model/login';
import { LoginService } from './login.service';
import { Router } from '@angular/router';
import { AppComponent } from '../app.component';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
 
  login = new Login;
  userId: number;
  UserName: string;
  password: string;
  constructor(private loginService:LoginService,private router:Router,private app:AppComponent) { }

  ngOnInit() {
    this.resetForm()
  }
  resetForm(form?:NgForm) {
    if(form!=null)
    form.resetForm();
    this.loginService.formData={
      userId:null,
      userName:'',
      password:'',
    }
  }

  // to call login validation on form submission

  OnSubmit(form:NgForm){
    this.validLogin(form);

  }
  validLogin(form:NgForm){
    this.loginService.validate(form.value).subscribe(data=>{
      this.login=data;
      this.router.navigate(['vendor']);
    }),
    console.log("error");
  }


}
